
import { PropType, defineComponent } from 'vue'
import { IbUserDetail } from '@/shared/type/CIB20100311.type'
import { CIB20100331_REQ_BODY, CIB20100331_RES } from '@/shared/type/CIB20100331.type'
import { modalController } from '@ionic/vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
  name: "cib201003ss01",
  props: {
    data: {
      type: Object as PropType<IbUserDetail>,
      required: true
    },
    currentTab: {
      type: String,
      required: true,
    },
  },

  data() {
    const userID = this.$route.query.userID;
    const customerNo = this.$route.query.customerNo;
    const approvalTap = this.$route.query.tab;
    return {
      userID,
      customerNo,
      approvalTap,
      serviceStatusCode:"",
      serviceCeaseReasonCode:"",
      isEditable: false,
      approvalRes: {} as any,
			operationMode: 'normal' as screenMode,
      tempData:{
        serviceStatusCode:'',
        serviceCeaseReasonCode:''
      }
    }
  },

  watch: {
    data(customerData: IbUserDetail) {
      if(this.approvalType == ''){
        this.serviceStatusCode= customerData.userInfo.serviceStatusCode;
        this.serviceCeaseReasonCode=  customerData.userInfo.serviceCeaseReasonCode;
        this.tempData.serviceStatusCode= customerData.userInfo.serviceStatusCode;
        this.tempData.serviceCeaseReasonCode=  customerData.userInfo.serviceCeaseReasonCode;
      }else{
        if(this.approvalTap == 'CIB201003SS01'){
          this.$approval.getAuthorizationDetail().then( (authDetail: APM20200113_RES) => {
          let authDetailParsed = JSON.parse(authDetail.body.requestContent);
          this.serviceStatusCode = authDetailParsed.serviceStatusCode
          this.serviceCeaseReasonCode = authDetailParsed.serviceCeaseReasonCode
          this.approvalRes = authDetail.body;
        })
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
        }
      }
    },
    
    isEditable(isAllowTabChange) {
      this.$emit('isBeingEdited', isAllowTabChange)
    }
  },

  computed:{
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
    },
  },

  methods: {
    onClickOperation(btn: string) {
      switch (btn) {
        case "edit": 
          this.isEditable = true;
          break;

        case "cancel": 
          this.serviceStatusCode = this.tempData.serviceStatusCode;
          this.serviceCeaseReasonCode = this.tempData.serviceCeaseReasonCode;
          this.isEditable = false;
          break;

        case "list":
          this.$router.push('/corporate-internet-banking/subscription');
          break;

        case "submit":
          this.$dialog.onOpen({
            content: `${this.$t("message.would_you_like_to_save")}`,
            dialogMode:"Question",
            callback:()=>{
              this.handleSubmit();
              modalController.dismiss();
            }
          })
          break;
      }
    },

    async handleSubmit() {
      const reqBody: CIB20100331_REQ_BODY = {
        userID: `${this.userID}`,
        customerNo: `${this.customerNo}`,
        serviceStatusCode: this.serviceStatusCode,
        serviceCeaseReasonCode: this.serviceCeaseReasonCode
      };

      const responseData = await this.$request.getRequest('CIB20100331', reqBody, false, 'CIB201003SS01') as CIB20100331_RES;
      if(this.approvalType){
        this.$emit("isReseTButton", false);
        this.isEditable = false;
      }
     
      if(responseData.header.result){
        this.$emit("isReseTButton", false);
        this.isEditable = false;
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode:"Success",
          callback:()=>{
            modalController.dismiss();
          }
        })
      }
    }
  }
})
